import React, { useContext } from "react";
import LangContext from "../contexts/LangContext";

const Address = () => {
  const { text } = useContext(LangContext);

  return (
    <>
      {/*  <p className="open-sans-font custom-span-contact position-relative">
        <i className="fa fa-map position-absolute"></i>
        <span className="d-block">Address Point</span>123 Stree New York City ,
        United States Of America 750065.
      </p> */}
      {/* End .custom-span-contact */}

      <p className="open-sans-font custom-span-contact position-relative">
        <i className="fa fa-envelope-open position-absolute"></i>
        <span className="d-block">{text.mailMe}</span>{" "}
        <a href={`mailto:${text.constants.email}`}>{text.constants.email}</a>
      </p>
      {/* End .custom-span-contact */}

      <p className="open-sans-font custom-span-contact position-relative">
        <i className="fa fa-phone-square position-absolute"></i>
        <span className="d-block">{text.callMe}</span>{" "}
        <a href={`Tel: ${text.constants.phone}`}>{text.constants.phone}</a>
      </p>
      {/* End .custom-span-contact */}
    </>
  );
};

export default Address;
