import React, { useContext } from "react";
import LangContext from "../../contexts/LangContext";

const PersonalInfo = () => {
  const { text } = useContext(LangContext);

  const personalInfoContent = [
    { meta: text.firstName, metaInfo: text.constants.firstName },
    { meta: text.lastName, metaInfo: text.constants.lastName },
    { meta: text.nationality, metaInfo: text.spanish },
    { meta: text.address, metaInfo: text.constants.address },
    { meta: text.phone, metaInfo: text.constants.phone },
    { meta: text.email, metaInfo: text.constants.email },
    //{ meta: "languages", metaInfo: "Spanish, English" },
    //{ meta: "age", metaInfo: "28" },
  ];

  return (
    <ul className="about-list list-unstyled open-sans-font">
      {personalInfoContent.map((val, i) => (
        <li key={i}>
          <span className="title">{val.meta}: </span>
          <span className="value d-block d-sm-inline-block d-lg-block d-xl-inline-block">
            {val.metaInfo}
          </span>
        </li>
      ))}
    </ul>
  );
};

export default PersonalInfo;
