import React, { useState, useContext } from "react";
import Modal from "react-modal";
import Index from "../about/Index";
// Contexts
import LangContext from "../../contexts/LangContext";
import ThemeContext from "../../contexts/ThemeContext";

const Hero = () => {
  window.scrollTo(0, 0);
  const { text } = useContext(LangContext);
  const { theme } = useContext(ThemeContext);

  const [isOpen, setIsOpen] = useState(false);
  function toggleModalOne() {
    setIsOpen(!isOpen);
  }

  return (
    <>
      <div className="row home-details-container align-items-center">
        {/* <div
          className="col-lg-4 bg position-fixed d-none d-lg-block"
          style={{
            backgroundImage: `url(${process.env.PUBLIC_URL + images.heroImage})`,
          }}
        ></div> */}
        <div className="col-12 col-lg-8 offset-lg-4 home-details  text-center text-lg-start">
          <div>
            {/* <img
              src={images.heroImageMobile}
              className="img-fluid main-img-mobile d-sm-block d-lg-none"
              alt="alt image"
            /> */}
            <h1 className="text-uppercase poppins-font">
              {text.im}
              <span>{text.designation}</span>
            </h1>
            {/* <p className="open-sans-font">{texts.description}</p> */}
            <button className="button" onClick={toggleModalOne}>
              <span className="button-text">{text.moreAboutMe}</span>
              <span className="button-icon fa fa-arrow-right"></span>
            </button>
          </div>
        </div>
      </div>
      {/* End home-details-container */}

      {/* Start Modal for About More */}
      <Modal
        isOpen={isOpen}
        onRequestClose={toggleModalOne}
        contentLabel="My dialog"
        className={theme.modalClassName}
        overlayClassName={theme.modalOverlayClassName}
        closeTimeoutMS={500}
      >
        <div>
          <button className="close-modal" onClick={toggleModalOne}>
            <img src="/img/cancel.svg" alt="close icon" />
          </button>
          {/* End close icon */}

          <div className={theme.aboutClassName}>
            <div data-aos="fade-up" data-aos-duration="1200">
              <div className="title-section text-left text-sm-center">
                <h1>
                  {text.about} <span>{text.me}</span>
                </h1>
                <span className="title-bg">{text.resume}</span>
              </div>
              {/* End title */}
              <Index />
            </div>
          </div>
        </div>
        {/* End modal box news */}
      </Modal>
      {/* End  Modal for About More */}
    </>
  );
};

export default Hero;
