import React, { useContext } from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import ModalDinoGame from "./modal/DinoGame";
import ModalIACelsius from "./modal/IACelsius";
import ModalExpenseIncomeControl from "./modal/ExpenseIncomeControl";
import ModalCatMemo from "./modal/CatMemo";
import ModalGifFinder from "./modal/GifFinder";

// Contexts
import LangContext from "../../contexts/LangContext";
import LiTemplate from "./liTemplate";

const Portfolio = () => {
  window.scrollTo(0, 0);
  const { text } = useContext(LangContext);

  return (
    <Tabs>
      <TabList className="portfolio-tab-list">
        <Tab>{text.all}</Tab>
        <Tab>{text.apps}</Tab>
        <Tab>{text.games}</Tab>
        <Tab>{text.neuronalNetworks}</Tab>
      </TabList>

      <div className="portfolio-tab-content">
        <TabPanel>
          <ul className="row grid justify-content-center">
            <LiTemplate
              image="/img/projects/dinosaur-game.png"
              title={text.dinosaurGame}
              modalComponent={<ModalDinoGame />}
            />
            <LiTemplate
              image="/img/projects/ia-celsius.png"
              title={text.IACelsius}
              modalComponent={<ModalIACelsius />}
            />
            <LiTemplate
              image="/img/projects/incomes-expenses-control.png"
              title={text.expenseIncomeControl}
              modalComponent={<ModalExpenseIncomeControl />}
            />
            <LiTemplate
              image="/img/projects/cat-memo.png"
              title={text.catMemo}
              modalComponent={<ModalCatMemo />}
            />
            <LiTemplate
              image="/img/projects/gif-finder.png"
              title={text.gifFinder}
              modalComponent={<ModalGifFinder />}
            />
          </ul>
        </TabPanel>

        <TabPanel>
          <ul className="row grid justify-content-center">
            <LiTemplate
              image="/img/projects/incomes-expenses-control.png"
              title={text.expenseIncomeControl}
              modalComponent={<ModalExpenseIncomeControl />}
            />
            <LiTemplate
              image="/img/projects/gif-finder.png"
              title={text.gifFinder}
              modalComponent={<ModalGifFinder />}
            />
          </ul>
        </TabPanel>

        <TabPanel>
          <ul className="row grid justify-content-center">
            <LiTemplate
              image="/img/projects/dinosaur-game.png"
              title={text.dinosaurGame}
              modalComponent={<ModalDinoGame />}
            />
            <LiTemplate
              image="/img/projects/cat-memo.png"
              title={text.catMemo}
              modalComponent={<ModalCatMemo />}
            />
          </ul>
        </TabPanel>

        <TabPanel>
          <ul className="row grid justify-content-center">
            <LiTemplate
              image="/img/projects/ia-celsius.png"
              title={text.IACelsius}
              modalComponent={<ModalIACelsius />}
            />
          </ul>
        </TabPanel>
      </div>
    </Tabs>
  );
};

export default Portfolio;
