import React, { useContext } from "react";
import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import Hero from "../components/hero/Hero";
import Index from "../components/about/Index";
import Address from "../components/Address";
import Portfolio from "../components/portfolio/Portfolio";
import Blog from "../components/blog/Blog";
import Contact from "../components/Contact";
import Social from "../components/Social";
// Contexts
import LangContext from "../contexts/LangContext";
import ThemeContext from "../contexts/ThemeContext";

const HomeDark = () => {
  const { text, handleChangeLang } = useContext(LangContext);
  const { theme, handleChangeTheme } = useContext(ThemeContext);

  theme.addClassLight ? document.body.classList.add('light') : document.body.classList.remove('light');

  const menuItem = [
    { icon: "fa-home", menuName: text.home },
    { icon: "fa-user", menuName: text.profile },
    { icon: "fa-briefcase", menuName: text.portfolio },
    { icon: "fa-envelope-open", menuName: text.contact },
    { icon: "fa-comments", menuName: text.blog },
  ];

  return (
    <div className={theme.color}>
      <div className="demo-sticker">
        <div onClick={handleChangeTheme}>
          <i className={theme.icon}></i>
        </div>
      </div>
      <div className="demo-sticker-flag">
        <div onClick={handleChangeLang}>
          <i className="fa fa-language"></i>
        </div>
      </div>
      <Tabs>
        <div className="header">
          <TabList className=" icon-menu  revealator-slideup revealator-once revealator-delay1">
            {menuItem.map((item, i) => (
              <Tab className="icon-box" key={i}>
                <i className={`fa ${item.icon}`}></i>
                <h2>{item.menuName}</h2>
              </Tab>
            ))}
          </TabList>
        </div>
        {/* End Menu Content */}

        <div className="tab-panel_list">
          {/* Hero Content Starts */}
          <TabPanel className="home ">
            <div
              className="container-fluid main-container container-home p-0 "
              data-aos="fade-up"
              data-aos-duration="1200"
            >
              <div className="color-block d-none d-lg-block"></div>
              <Hero />
            </div>
          </TabPanel>
          {/* Hero Content Ends */}

          {/* About Content Starts */}
          <TabPanel className="about">
            <div data-aos="fade-up" data-aos-duration="1200">
              <div className="title-section text-left text-sm-center">
                <h1>
                  {text.about} <span>{text.me}</span>
                </h1>
                <span className="title-bg">{text.resume}</span>
              </div>
              {/* End title */}
              <Index />
            </div>
          </TabPanel>
          {/* About Content Ends */}

          {/* Portfolio Content Starts */}
          <TabPanel className="portfolio">
            <div
              className="title-section text-left text-sm-center"
              data-aos="fade-up"
              data-aos-duration="1200"
            >
              <h1>
                {text.my} <span>{text.portfolio}</span>
              </h1>
              <span className="title-bg">{text.works}</span>
            </div>
            {/* End title */}
            <div
              className="container grid-gallery main-content"
              data-aos="fade-up"
              data-aos-duration="1200"
              data-aos-delay="100"
            >
              <Portfolio />
            </div>
            {/* End grid gallery */}
          </TabPanel>
          {/* Portfolio Content Ends */}

          {/* Contact Content Starts */}
          <TabPanel className="contact">
            <div
              className="title-section text-left text-sm-center"
              data-aos="fade-up"
              data-aos-duration="1200"
            >
              <h1>
                {text.contactTitle1}<span>{text.contactTitle2}</span>
              </h1>
              <span className="title-bg">{text.contactSubtitle}</span>
            </div>
            <div
              className="container"
              data-aos="fade-up"
              data-aos-duration="1200"
            >
              <div className="row">
                {/*  Left Side Starts */}
                <div className="col-12 col-lg-4">
                  <h3 className="text-uppercase custom-title mb-0 ft-wt-600 pb-3">
                    {text.contactContentTitle}
                  </h3>
                  <p className="open-sans-font mb-4 justify">
                    {text.contactContentSubtitle}
                  </p>
                  <Address />
                  {/* End Address */}

                  <Social />
                  {/* End Social */}
                </div>
                {/* Left Side Ends */}

                {/*  Contact Form Starts  */}
                <div className="col-12 col-lg-8">
                  <Contact />
                </div>
                {/*  Contact Form Ends */}
              </div>
            </div>
            {/* End .container */}
          </TabPanel>
          {/* Contact Content Ends */}

          {/* Blog Content Starts */}
          <TabPanel className="blog">
            <div
              className="title-section text-left text-sm-center "
              data-aos="fade-up"
              data-aos-duration="1200"
            >
              <h1>
                {text.my} <span>{text.blog}</span>
              </h1>
              <span className="title-bg">{text.posts}</span>
            </div>
            <div
              className="container"
              data-aos="fade-up"
              data-aos-duration="1200"
            >
              {/*  Articles Starts  */}
              <div className="row pb-50">
                <Blog />
              </div>
              {/* Articles Ends */}
            </div>
          </TabPanel>
          {/* Blog Content Ends */}
        </div>
      </Tabs>
    </div>
  );
};

export default HomeDark;
